import React from "react";
import _t from "../../services/i18n.service";

function QuestionCard (props) {
    let question = {...props.question};   

    // Date handling
    const deadline_date = new Date(question.deadline);
    const deadline = deadline_date.toLocaleDateString();
    const created_on_date = new Date(question.created_on);
    const created_on = created_on_date.toLocaleDateString();

    return (
        <div className="card row mt-4 mx-4"  onClick={() => props.onEdit(question)}>
            <div className={"card-header " + 
                            (question.state === 'editable' ? "bg-primary text-light" : 
                             question.state === 'pending' ? "bg-warning" : 
                             question.state === 'answered' ? "bg-success text-light" : '')}> 
                <div className="row">
                    <div className="col-9">{question.title}</div> 
                    <div className="col-3 d-flex justify-content-end">
                        {/*
                        {question.state === 'editable' && (<i className="bi bi-pencil-square"> {_t('edit')}</i>)}
                        {question.state === 'pending' && (<i className="bi bi-hourglass-split"> {_t('pending')}</i>)}
                        {question.state === 'answered' && (<i className="bi bi-check2-circle"> {_t('answered')}</i>)}
                        {question.state === 'unanswered' && (<i className="bi bi-x-circle"> {_t('unanswered')}</i>)}
                        */}
                        {question.state === 'editable' && (<i className="bi bi-pencil-square"></i>)}
                        {question.state === 'pending' && (<i className="bi bi-hourglass-split"></i>)}
                        {question.state === 'answered' && (<i className="bi bi-check2-circle"></i>)}
                        {question.state === 'unanswered' && (<i className="bi bi-x-circle"></i>)}
                    </div>
                </div>
            </div>
            <div className="card-body">
        <div className="row">
          <div className="col-6">
            <p>{question.description}</p>
          </div>
          <div className="col-6">
            <i className="d-flex justify-content-end">
              {_t("phase")} {question.phase} {question.pre_post}
            </i>
          </div>
        </div>
        <div className="row">
          <div className="col-6">
            <span className="card-title d-flex justify-content-start">
                <i className="bi bi-calendar-check me-1"></i> {' '} {created_on}
            </span>
          </div>
          <div className="col-4">
            <span className="card-title d-flex justify-content-end">
                <i className="bi bi-calendar-x me-1"></i> {' ' +  deadline}
            </span>
          </div>
          <div className="col-2 d-flex justify-content-end">
            {(question.state === 'editable' || question.state === 'pending') && (<i className="bi bi-chevron-right"></i>)}
          </div>
        </div>
      </div>
           
        </div>
    );
}

export default QuestionCard;