import axios from "axios";
import AuthService from "./auth.service";
import config from "../config";
//import { professor_data, student_data, classroom_data } from "./fake-content";
import _t from "./i18n.service";

class BackendService {
  bearerConfig() {
    const user = JSON.parse(localStorage.getItem("user"));
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    };
    return config;
  }

  updateJWT(res) {
    if (res && res.data && res.data.jwt) {
      const user = {
        ...JSON.parse(localStorage.getItem("user")),
        jwt: res.data.jwt,
      };
      localStorage.setItem("user", JSON.stringify(user));
    }
  }

  changePassword(email, password, new_password) {
    return new Promise((resolve, reject) => {
      axios
        .post(
          config.API_URL + "/change-password",
          {
            email: email,
            password: password,
            newPassword: new_password,
          },
          this.bearerConfig()
        )
        .then((res) => {
          this.updateJWT(res);
          resolve({ message: _t("password_changed") });
        })
        .catch((err) => {
          reject({ message: _t("password_change_" + err.response.status) });
        });
    });
  }

  getSettings() {
    return axios
      .get(config.API_URL + "/question-settings")
      .then((res) => res.data.settings[0])
      .catch((err) => err);
  }

  getClassrooms() {
    return axios
      .get(config.API_URL + "/classrooms/user", this.bearerConfig())
      .then((res) => res.data.classrooms)
      .catch((err) => {throw err});
  }

  // --------------------------------------------------------------------------
  // PROFESSOR API CALLS
  // --------------------------------------------------------------------------

  async getProfessorQuestions(code) {
    return axios
      .get(
        config.API_URL + "/questions/" + code,
        this.bearerConfig()
      )
      .then((res) => {
        const questions = res.data.classrooms.sort((a, b) => b.id - a.id); // newests questions first // TODO change res.data.classrooms to res.data.questions
        return questions;
      })
      .catch((err) => {throw err});
  }

  newClassroom(title) {
    return new Promise((resolve, reject) => {
      axios
        .post(
          config.API_URL + "/classroom/create",
          { title: title },
          this.bearerConfig()
        )
        .then((res) => {
          resolve(res.data);
        })
        .catch((err) => reject(err));
    });
  }

  delClassroom(code) {
    return new Promise((resolve, reject) => {
      axios
        .delete(
          config.API_URL + "/classroom/delete/" + code,
          this.bearerConfig()
        )
        .then((res) => {
          this.updateJWT(res);
          resolve(res);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }

  newQuestion(question) {
    return new Promise((resolve, reject) => {
      axios
        .post(
          config.API_URL + "/question/create",
          question,
          this.bearerConfig()
        )
        .then((res) => {
          resolve(res.data);
        })
        .catch((err) => reject(err));
    });
  }

  delQuestion(id) {
    return new Promise((resolve, reject) => {
      axios
        .delete(
          config.API_URL + "/question/delete/" + `${id}`,
          this.bearerConfig()
        )
        .then((res) => {
          this.updateJWT(res);
          resolve(res);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }

  // --------------------------------------------------------------------------
  // STUDENT API CALLS
  // --------------------------------------------------------------------------

  getStudentQuestions(code) {
    const user = AuthService.getCurrentUser();
    return axios
      .get(config.API_URL + "/questions/" + code, this.bearerConfig())
      .then(async res => {
        let questions = res.data.classrooms; // this may change to res.data.questions
        for (let i=0; i<questions.length; i++) {
          let question = questions[i];
          let answer = await axios.get(config.API_URL + "/answer/" + user.email + "/" + question.Question_id, this.bearerConfig());
          question.response = answer.data.response;
          question.answer_time = answer.data.timestamp ? answer.data.timestamp : undefined; // TODO repasar esto cuando la API añada el timestamp
          const deadline = new Date(question.deadline);
          const answered = question.response !== -1;
          const deadline_passed = new Date() > deadline;
          
          if (answered) {
            const merit_time = new Date(question.answer_timestamp) + 60*60*1000;
            const merit_time_passed = new Date() > merit_time;
            question.state = deadline_passed ? "answered" : merit_time_passed ? "answered" : "editable";
          } else {
            question.state = deadline_passed ? "unanswered" : "pending";
          }

          questions[i] = question;
        }
        return questions;
      }); 
  }

  subscribeToClassroom(code, email) {
    return axios
      .post(
        config.API_URL + "/enrolment/create",
        { 
          classroomCode: code, 
          userEmail: email 
        },
        this.bearerConfig()
      )
      .then((res) => res.data.data)
      .catch((err) => {
        console.log(err);
        return ({status: err.response.status});
      });
  }

  answerQuestion(level, question_id) {
    const user = AuthService.getCurrentUser();
    
    return axios
      .post(
        config.API_URL + "/answer/submit",
        {
          UserId: user.user_id,
          QuestionId: question_id,
          selected_option: level,
        },
        this.bearerConfig()
      )
      .then(() => {})
      .catch(err => {throw err});
  }

  delUser(email, password) {
    return new Promise((resolve, reject) => {
      axios
        .delete(config.API_URL + "/purge-data", {
          data: {
            email: email,
            password: password,
          },
          ...this.bearerConfig(),
        })
        .then(resolve({ message: _t("user_deleted") }))
        .catch(reject({ message: _t("operation_failed") }));
    });
  }
}

export default new BackendService();
