import React, { Component } from "react";
import _t from "../../services/i18n.service";
import Question from './question.component';
import QuestionCard from './question_card.component';
import ClassroomCard from "./classroom_card.component";
import BackendService from "../../services/backend.service";
import { Message, showMsg } from "../message.component";

class Classroom extends Component  {

    constructor(props) {
        super(props);
        this.onEdit = this.onEdit.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.setMsg = this.setMsg.bind(this);

        this.state = {
            show: 'classroom',
            question: null,
            message: {show: false}
        };
    }

    setMsg(message) {
        this.setState({message: message});
    }

    onEdit (q) {
        this.setState({
            show: 'question',
            question: q
        });
    }

    onSubmit (level, question_id) {
        BackendService.answerQuestion(level, question_id)
        .then(res => { 
            this.setState({show: 'classroom'});
            showMsg(_t("answer_recorded"), "success", this.setMsg);
            setTimeout(() => {}, 20000);
        })
        .catch(err => {
            this.setState({show: 'classroom'});
            showMsg(_t("operation_failed"), "danger", this.setMsg);
            setTimeout(() => {}, 20000);            
        });
    }

    render () {
        const classroom = this.props.classroom;

        return (
            <div>
            {this.state.message.show && (
            <Message text={this.state.message.text} type={this.state.message.type} />
            )}                
            {(this.state.show === 'classroom') &&
            (<div>
                <ClassroomCard header={true} classroom={classroom}/>
                <h5 className="mt-2">{_t('questions')}:</h5>
                {classroom.questions.length > 0 && classroom.questions.map((q,i) =>
                    <QuestionCard key={i} question={q} onEdit={this.onEdit}/>
                )}
                {classroom.questions.length === 0 && 
                (
                    <p>{_t("no_questions")}</p>
                )}
            </div>
            )}
            {(this.state.show === 'question') &&
            (
                <Question onSubmit={this.onSubmit} onClose={() => this.setState({show: 'classroom'})} question={this.state.question} />
            )}
            </div>
        );
    }
}

export default Classroom;